import React from 'react'

export interface DemoProps extends React.Props<{}> {
  className?: string
  language?: string
  code: string
  description: string
  title: string
}

class Demo extends React.Component<DemoProps> {
  render() {
    const { children, description, title, className, language, code } = this.props
    return <section className={`${className} wrapper`}>
      <div className='container'>{children}</div>
      <div className='description'>
        <span className='title'>{title}</span>
        <span>{description}</span>
      </div>
      <div className='code'>
        <pre className={`language-${language || 'jsx'}`}>
          <code className={`language-${language || 'jsx'}`}>
            {code}
          </code>
        </pre>
      </div>
      <style jsx>{`
        .wrapper{
          box-sizing: border-box;
          border: 1px solid #e8e8e8;
          border-radius: 4px;
        }
        .container{
          padding: 30px;
        }
        .description{
          border-top: 1px solid #e8e8e8;
          padding: 20px 30px;
          position: relative;
          color: #6A737D;
          font-size: 14px;
        }
        .description .title{
          position: absolute;
          background-color: #fff;
          padding: 5px 10px;
          left:20px;
          top:0;
          color: #314659;
          font-weight: 500;
          font-size: 16px;
          transform: translateY(-50%);
        }
        pre{
          border-top: 1px dashed #e8e8e8;
          margin:0;
          background: transparent;
        }
      `}</style>
    </section >
  }
}

export default Demo